.control-tray {
  position: fixed;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  gap: 1rem;
  z-index: 200;
  padding: 0.75rem;
  background: rgba(18, 18, 26, 0.8);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 2rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  animation: slideUpTray 0.5s ease-out;
}

.actions-nav {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.5rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 1.5rem;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  
  &.disabled {
    opacity: 0.5;
    pointer-events: none;
  }
}

.action-button {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3rem;
  height: 3rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 1rem;
  color: rgba(255, 255, 255, 0.7);
  font-size: 1.25rem;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  user-select: none;
  overflow: hidden;
  
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(135deg, rgba(0, 212, 255, 0.1), rgba(124, 58, 237, 0.1));
    opacity: 0;
    transition: opacity 0.3s ease;
    border-radius: inherit;
  }
  
  &:hover {
    transform: translateY(-2px);
    border-color: rgba(0, 212, 255, 0.3);
    color: rgba(255, 255, 255, 0.9);
    box-shadow: 0 8px 25px rgba(0, 212, 255, 0.2);
    
    &:before {
      opacity: 1;
    }
  }
  
  &:focus {
    outline: none;
    border-color: rgba(0, 212, 255, 0.5);
    box-shadow: 0 0 0 2px rgba(0, 212, 255, 0.2);
  }
  
  &.outlined {
    background: rgba(255, 255, 255, 0.02);
    border: 2px solid rgba(255, 255, 255, 0.1);
  }
  
  &.no-action {
    pointer-events: none;
    cursor: default;
    
    &:hover {
      transform: none;
      box-shadow: none;
    }
  }
  
  .material-symbols-outlined {
    position: relative;
    z-index: 1;
    font-size: 1.25rem;
  }
}

/* Microphone Button */
.mic-button {
  background: linear-gradient(135deg, #ff4757, #ff3742);
  border-color: rgba(255, 71, 87, 0.3);
  color: white;
  position: relative;
  overflow: visible;
  
  &:before {
    position: absolute;
    content: '';
    top: calc(var(--volume, 0px) * -1);
    left: calc(var(--volume, 0px) * -1);
    right: calc(var(--volume, 0px) * -1);
    bottom: calc(var(--volume, 0px) * -1);
    background: radial-gradient(circle, rgba(255, 71, 87, 0.4) 0%, transparent 70%);
    border-radius: 50%;
    z-index: -1;
    transition: all 0.1s ease;
    animation: micPulse 2s ease-in-out infinite;
  }
  
  &:hover {
    background: linear-gradient(135deg, #ff6b7a, #ff5252);
    transform: translateY(-2px) scale(1.05);
    box-shadow: 0 8px 25px rgba(255, 71, 87, 0.4);
  }
  
  &:focus {
    border-color: rgba(255, 71, 87, 0.6);
    box-shadow: 0 0 0 2px rgba(255, 71, 87, 0.3);
  }
  
  &.disabled {
    background: rgba(255, 255, 255, 0.05);
    color: rgba(255, 255, 255, 0.3);
    
    &:before {
      display: none;
    }
  }
}

/* Connection Button */
.connect-toggle {
  background: linear-gradient(135deg, #00d4ff, #0099cc);
  border-color: rgba(0, 212, 255, 0.3);
  color: white;
  font-weight: 600;
  
  &:hover {
    background: linear-gradient(135deg, #33ddff, #00aadd);
    transform: translateY(-2px) scale(1.05);
    box-shadow: 0 8px 25px rgba(0, 212, 255, 0.4);
  }
  
  &:focus {
    border-color: rgba(0, 212, 255, 0.6);
    box-shadow: 0 0 0 2px rgba(0, 212, 255, 0.3);
  }
  
  &.connected {
    background: linear-gradient(135deg, #06ffa5, #00cc7a);
    border-color: rgba(6, 255, 165, 0.3);
    
    &:hover {
      background: linear-gradient(135deg, #39ffb8, #00dd88);
      box-shadow: 0 8px 25px rgba(6, 255, 165, 0.4);
    }
  }
}

/* Connection Container */
.connection-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  
  .connection-button-container {
    position: relative;
  }
  
  .text-indicator {
    font-size: 0.75rem;
    color: rgba(0, 212, 255, 0.8);
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    opacity: 0;
    transform: translateY(10px);
    transition: all 0.3s ease;
  }
  
  &.connected {
    .text-indicator {
      opacity: 1;
      transform: translateY(0);
    }
  }
}

/* Volume Visualization */
@property --volume {
  syntax: "length";
  inherit: false;
  initial-value: 0px;
}

/* Enhanced Animations */
@keyframes slideUpTray {
  from {
    transform: translateX(-50%) translateY(100px);
    opacity: 0;
  }
  to {
    transform: translateX(-50%) translateY(0);
    opacity: 1;
  }
}

@keyframes micPulse {
  0%, 100% {
    opacity: 0.3;
    transform: scale(1);
  }
  50% {
    opacity: 0.6;
    transform: scale(1.1);
  }
}

@keyframes connectionGlow {
  0%, 100% {
    box-shadow: 0 0 20px rgba(0, 212, 255, 0.2);
  }
  50% {
    box-shadow: 0 0 30px rgba(0, 212, 255, 0.4);
  }
}

/* Disabled States */
.disabled {
  .action-button {
    background: rgba(255, 255, 255, 0.02) !important;
    border-color: rgba(255, 255, 255, 0.05) !important;
    color: rgba(255, 255, 255, 0.3) !important;
    cursor: not-allowed;
    
    &:hover {
      transform: none !important;
      box-shadow: none !important;
      
      &:before {
        opacity: 0 !important;
      }
    }
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .control-tray {
    bottom: 1rem;
    padding: 0.5rem;
    gap: 0.75rem;
  }
  
  .actions-nav {
    gap: 0.5rem;
    padding: 0.375rem;
  }
  
  .action-button {
    width: 2.5rem;
    height: 2.5rem;
    font-size: 1rem;
    
    .material-symbols-outlined {
      font-size: 1rem;
    }
  }
  
  .text-indicator {
    font-size: 0.625rem;
  }
}

@media (max-width: 480px) {
  .control-tray {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .connection-container {
    order: -1;
  }
}
